.header {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  background-color: transparentize($color: $fifthColor, $amount: 0.8);
  padding: 20px 50px 20px 50px;
  max-height: 30%;

  &__logo {
    top: 40px;
    width: 500px;
    height: 200px;
  }
  &__nav {
    min-width: 400px;
    height: 50px;
    display: flex;
    flex-flow: row;
    justify-content: flex-end;

    &--menu {
      display: flex;
      flex-flow: row;
      justify-content: flex-end;
      list-style: none;
    }
    & li {
      width: 250px;

      text-align: right;
    }
    & a {
      text-decoration: none;

      color: $secondColor;

      margin-left: 10px;
      margin-right: 10px;
      font-family: "Montserrat";
      font-weight: 200;
      font-size: 30px;
    }
    & p {
      height: 50px;
    }
  }
  & li:hover {
    cursor: pointer;
  }
}
