body,
html,
#root {
  width: 100%;
  height: 100%;

  margin: 0px;
  padding: 0px;
  font-family: "Montserrat";
}
.body {
  display: block;

  min-height: 100%;
  padding-bottom: 30px;
}
a {
  text-decoration: none;
  &:hover {
    cursor: pointer;
  }
}
li {
  text-decoration: none;
}

.mainHome,
.mainProduct,
.mainContact {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}
