.container__head {
  height: 100%;
}

.container__prez {
  display: flex;
  justify-content: space-between;
  flex-flow: column;
  height: 100%;
  margin-left: 50px;
  margin-right: 50px;
  font-family: "Montserrat";
  &--title {
    text-align: center;
    font-family: "Montserrat";
    font-weight: 700;
    height: 150px;
    & h1 {
      margin: 50px auto;
      justify-self: center;
      font-weight: 400;
      font-size: 50px;
    }
  }
  &--model {
    display: flex;
    flex-flow: row;
    justify-content: space-evenly;
  }

  &--list {
    list-style-type: none;
    text-align: center;
    line-height: 40px;
    width: 200px;
    & button {
      border: none;
      background-color: transparent;
      min-width: 200px;
      cursor: pointer;
    }
    & h2 {
      background-color: $fourthColor;
      border-radius: 10px;
      border-top-left-radius: 0px;
      color: white;
      font-family: "Montserrat";
      margin: 0;
      height: 40px;
      line-height: 40px;
    }
    & p {
      text-decoration: none;
      font-family: "Montserrat";
      color: $thirdColor;
      font-size: 20px;
      margin: 0;
      font-weight: bold;
    }
  }
}

.container__prez {
  &--text {
    width: 95%;
    background-color: $fifthColor;
    text-align: center;
    font-weight: 500;
    font-family: "Montserrat";
    font-size: 30px;
    height: 500px;
    margin-left: 100px;
  }
  &--image {
    width: 100%;
    height: 500px;
    color: white;
    font-weight: 500;
    font-family: "Montserrat";
    font-size: 30px;
    text-align: center;
    background-color: $firstColor;
    margin-right: 5px;
  }
}
.container__feat {
  margin-bottom: 100px;
  background-color: $fifthColor;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  padding-left: 30px;
}
