.container__form {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  height: 100%;
}
form {
  display: flex;
  flex-flow: column;
  margin-left: 100px;
  margin-right: 100px;
  justify-content: space-between;
  & label {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 30px;
    & textarea {
      height: 200px;

      padding-left: 20px;
      width: 80%;
      font-size: 26px;
      font-family: "Montserrat";
      color: white;
      background-color: $fifthColor;
      border-radius: 10px;
    }
  }
  & p {
    font-family: "Montserrat";
    text-align: right;
    font-weight: 500;
    font-size: 24px;
    padding-right: 30px;
    margin: 0 auto;
    border-radius: 10px;
    line-height: 40px;
    height: 40px;
    word-wrap: break-word;
    width: 15%;
    color: white;
    background-color: $secondColor;
  }
  & input {
    border-style: none;
    color: white;
    font-family: "Montserrat";
    font-size: 26px;
    padding-left: 20px;
    padding-right: 0px;
    background-color: $fifthColor;
    border-radius: 10px;
    margin: 0 auto;
    width: 80%;
    height: 40px;
  }
}
.Form__submit {
  width: 200px;
  font-weight: 600;
  background-color: $thirdColor;
}
