.mainHome__text {
  position: relative;
  width: 100%;
  height: auto;
}
.container__description,
.container__product {
  padding-top: 40px;
  padding-left: 50px;
  padding-right: 50px;
  & h1 {
    font-family: "Montserrat";
    font-size: 36px;
  }
  & h2 {
    font-family: "Montserrat";
    font-weight: 500;
    font-size: 30px;
  }
  & p {
    font-family: "Montserrat";
    font-size: 24px;
    text-align: justify;
  }
}

.container__product--gallery {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  margin-left: 150px;
  margin-right: 150px;
  margin-bottom: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

.gallery__card {
  width: 400px;
  height: 350px;
  background-color: $secondColor;
  display: flex;
  flex-flow: column;
  padding-top: 20px;
  margin-bottom: 40px;
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
  border-top-right-radius: 40px;

  &--img {
    height: 280px;
    width: 90%;
    margin: 0 auto;
    border: $fifthColor 1px solid;
    border-radius: 20px;
    text-align: center;
    color: white;
    background-size: cover;
  }
  & h3 {
    margin-top: 5px;
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 26px;
    line-height: 26px;
    color: white;
    padding-left: 40px;
  }
}
.article__wellfare {
  position: relative;
  padding-left: 50px;
  padding-right: 50px;
  margin-bottom: 100px;
  & h2 {
    font-family: "Montserrat";
    font-weight: 500;
    font-size: 30px;
  }
  & p {
    font-family: "Montserrat";
    font-size: 24px;
  }
}
