@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,400;1,700&display=swap");
body,
html,
#root {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  font-family: "Montserrat";
}

.body {
  display: block;
  min-height: 100%;
  padding-bottom: 30px;
}

a {
  text-decoration: none;
}
a:hover {
  cursor: pointer;
}

li {
  text-decoration: none;
}

.mainHome,
.mainProduct,
.mainContact {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}

.header {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  background-color: rgba(20, 154, 192, 0.2);
  padding: 20px 50px 20px 50px;
  max-height: 30%;
}
.header__logo {
  top: 40px;
  width: 500px;
  height: 200px;
}
.header__nav {
  min-width: 400px;
  height: 50px;
  display: flex;
  flex-flow: row;
  justify-content: flex-end;
}
.header__nav--menu {
  display: flex;
  flex-flow: row;
  justify-content: flex-end;
  list-style: none;
}
.header__nav li {
  width: 250px;
  text-align: right;
}
.header__nav a {
  text-decoration: none;
  color: #0e4194;
  margin-left: 10px;
  margin-right: 10px;
  font-family: "Montserrat";
  font-weight: 200;
  font-size: 30px;
}
.header__nav p {
  height: 50px;
}
.header li:hover {
  cursor: pointer;
}

.footer {
  position: relative;
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  background-color: #0e4194;
  min-height: 20%;
  color: white;
}
.footer__container {
  width: 40%;
  margin-top: auto;
  margin-bottom: auto;
}
.footer__logo {
  position: relative;
  left: 30px;
  width: 350px;
}
.footer__adress {
  width: 30%;
  display: flex;
  flex-flow: column;
  font-family: "Montserrat";
  font-weight: 500;
  line-height: 10px;
  margin-top: auto;
  margin-bottom: auto;
  border-left: 1px solid white;
  padding-left: 20px;
}
.footer__adress--contact {
  display: flex;
  color: white;
  flex-flow: row;
}
.footer__adress--contact i {
  width: 50px;
  color: white;
  line-height: 30px;
}
.footer__adress--mail {
  width: 50px;
  line-height: 30px;
  color: white;
}
.footer__network {
  width: 30%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  text-align: center;
  font-size: 35px;
}
.footer__network a {
  color: white;
}
.footer__network :hover {
  cursor: pointer;
}
.footer__network i {
  width: 50%;
  padding: 20px;
  margin: 0 auto;
}
.footer__network p {
  font-family: "Montserrat";
  font-size: 20px;
  width: 50%;
  margin: 0 auto;
}

.imageGallery {
  width: 100%;
  height: 500px;
  background-image: url(/src/Assets/IMG_8450.webp);
  background-size: cover;
  background-position-y: 50%;
}

.mainHome__text {
  position: relative;
  width: 100%;
  height: auto;
}

.container__description,
.container__product {
  padding-top: 40px;
  padding-left: 50px;
  padding-right: 50px;
}
.container__description h1,
.container__product h1 {
  font-family: "Montserrat";
  font-size: 36px;
}
.container__description h2,
.container__product h2 {
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 30px;
}
.container__description p,
.container__product p {
  font-family: "Montserrat";
  font-size: 24px;
  text-align: justify;
}

.container__product--gallery {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  margin-left: 150px;
  margin-right: 150px;
  margin-bottom: 40px;
  padding-left: 20px;
  padding-right: 20px;
}

.gallery__card {
  width: 400px;
  height: 350px;
  background-color: #0e4194;
  display: flex;
  flex-flow: column;
  padding-top: 20px;
  margin-bottom: 40px;
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
  border-top-right-radius: 40px;
}
.gallery__card--img {
  height: 280px;
  width: 90%;
  margin: 0 auto;
  border: #149ac0 1px solid;
  border-radius: 20px;
  text-align: center;
  color: white;
  background-size: cover;
}
.gallery__card h3 {
  margin-top: 5px;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 26px;
  line-height: 26px;
  color: white;
  padding-left: 40px;
}

.article__wellfare {
  position: relative;
  padding-left: 50px;
  padding-right: 50px;
  margin-bottom: 100px;
}
.article__wellfare h2 {
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 30px;
}
.article__wellfare p {
  font-family: "Montserrat";
  font-size: 24px;
}

.container__head {
  height: 100%;
}

.container__prez {
  display: flex;
  justify-content: space-between;
  flex-flow: column;
  height: 100%;
  margin-left: 50px;
  margin-right: 50px;
  font-family: "Montserrat";
}
.container__prez--title {
  text-align: center;
  font-family: "Montserrat";
  font-weight: 700;
  height: 150px;
}
.container__prez--title h1 {
  margin: 50px auto;
  justify-self: center;
  font-weight: 400;
  font-size: 50px;
}
.container__prez--model {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
}
.container__prez--list {
  list-style-type: none;
  text-align: center;
  line-height: 40px;
  width: 200px;
}
.container__prez--list button {
  border: none;
  background-color: transparent;
  min-width: 200px;
  cursor: pointer;
}
.container__prez--list h2 {
  background-color: #0a74bb;
  border-radius: 10px;
  border-top-left-radius: 0px;
  color: white;
  font-family: "Montserrat";
  margin: 0;
  height: 40px;
  line-height: 40px;
}
.container__prez--list p {
  text-decoration: none;
  font-family: "Montserrat";
  color: #215ca8;
  font-size: 20px;
  margin: 0;
  font-weight: bold;
}

.container__prez--text {
  width: 95%;
  background-color: #149ac0;
  text-align: center;
  font-weight: 500;
  font-family: "Montserrat";
  font-size: 30px;
  height: 500px;
  margin-left: 100px;
}
.container__prez--image {
  width: 100%;
  height: 500px;
  color: white;
  font-weight: 500;
  font-family: "Montserrat";
  font-size: 30px;
  text-align: center;
  background-color: #242e60;
  margin-right: 5px;
}

.container__feat {
  margin-bottom: 100px;
  background-color: #149ac0;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  padding-left: 30px;
}

.container__form {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  height: 100%;
}

form {
  display: flex;
  flex-flow: column;
  margin-left: 100px;
  margin-right: 100px;
  justify-content: space-between;
}
form label {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  margin-bottom: 30px;
}
form label textarea {
  height: 200px;
  padding-left: 20px;
  width: 80%;
  font-size: 26px;
  font-family: "Montserrat";
  color: white;
  background-color: #149ac0;
  border-radius: 10px;
}
form p {
  font-family: "Montserrat";
  text-align: right;
  font-weight: 500;
  font-size: 24px;
  padding-right: 30px;
  margin: 0 auto;
  border-radius: 10px;
  line-height: 40px;
  height: 40px;
  word-wrap: break-word;
  width: 15%;
  color: white;
  background-color: #0e4194;
}
form input {
  border-style: none;
  color: white;
  font-family: "Montserrat";
  font-size: 26px;
  padding-left: 20px;
  padding-right: 0px;
  background-color: #149ac0;
  border-radius: 10px;
  margin: 0 auto;
  width: 80%;
  height: 40px;
}

.Form__submit {
  width: 200px;
  font-weight: 600;
  background-color: #215ca8;
}

.error {
  max-width: 100%;
  height: 800px;
  top: 160px;
  margin: 0 auto;
  font-family: "Montserrat";
  font-style: normal;
  background: #0a74bb;
  color: #242e60;
  text-align: center;
}
.error__number {
  height: 263px;
  top: 277px;
  font-weight: 700;
  font-size: 288px;
  line-height: 411px;
}
@media screen and (max-width: 750px) {
  .error__number {
    height: 99px;
    top: 262px;
  }
}
.error__text {
  height: 51px;
  top: 606px;
  font-weight: 500;
  font-size: 36px;
  line-height: 51px;
}
@media screen and (max-width: 750px) {
  .error__text {
    text-align: center;
    width: 75%;
    top: 372px;
    margin: 0 auto;
  }
}
.error__link {
  top: 839px;
  height: 26px;
  font-weight: 500;
  font-size: 18px;
  line-height: 51px;
}
.error__link a {
  color: white;
}

